.modal{position:fixed;top:0;left:0;z-index:999;padding:0 10px;height:100vh;width:100%;background-color:RGBA(33, 33, 33, 0.7);overflow-y:auto;overflow-x:hidden}.modal .wrapper{margin:40px auto;padding:30px 60px;max-width:768px;height:max-content;background-color:#f4f5f7;border-radius:4px;position:relative}.modal .wrapper .head{position:relative;margin-bottom:20px}.modal .wrapper .head .close{position:absolute;right:0;display:block;width:20px;height:20px;cursor:pointer}.modal .wrapper .head .close::before{content:"";font-family:"icomoon";font-size:24px;font-weight:100;color:#42526e;transition:filter .2s}.modal .wrapper .head .close:hover::before{filter:brightness(1.2)}.modal .wrapper .head::before{content:"";position:absolute;width:24px;height:24px;top:14px;left:-30px;transform:translateY(-50%);font-family:"icomoon"}.modal .wrapper .content{padding-top:20px;display:flex;justify-content:space-between;flex-wrap:wrap;gap:0 20px}@media screen and (max-width: 500px){.modal .wrapper .content{flex-direction:column-reverse;gap:20px 0}}.modal .wrapper .content .main{flex-grow:1}.modal .wrapper .content .main .descr .descrTitle{display:block;position:relative;margin-bottom:14px}.modal .wrapper .content .main .descr .descrTitle::before{content:"";position:absolute;width:24px;height:24px;top:50%;left:-30px;transform:translateY(-50%);font-family:"icomoon";font-weight:400}.modal .wrapper .content .main .descr .descrBody{cursor:pointer}.modal .wrapper .content .main .chelists{display:flex;flex-direction:column;gap:40px 0;padding:40px 0}.modal .wrapper .content .aside{min-width:170px}.modal .wrapper .content .aside .nav{display:flex;flex-direction:column;gap:8px 0}@media screen and (max-width: 500px){.modal .wrapper .content .aside .nav{flex-direction:row;justify-content:center;align-items:stretch;gap:0 20px}}.modal .wrapper .content .aside .nav .navitem{border-radius:4px;background-color:#eaecf0;display:flex;align-items:center;justify-content:center}.modal .wrapper .content .aside .nav .navitem:hover{background-color:#e2e4e9}.modal .wrapper .loadWrappper{position:absolute;display:flex;justify-content:center;backdrop-filter:blur(10px);top:0;left:0;width:100%;height:100%}