.checkList .head{position:relative;display:flex;align-items:flex-start;gap:0 16px}.checkList .head::before{content:"";position:absolute;left:-30px;width:24px;height:24px;font-family:"icomoon"}.checkList .task{padding:8px;display:flex;align-items:flex-start;gap:0 20px;border-radius:4px;position:relative;transition:background-color .2s;cursor:pointer}.checkList .task .tasksClose{position:absolute;display:none;right:8px;top:4px;width:16px;height:16px;cursor:pointer}.checkList .task .tasksClose::before{content:"";font-family:"icomoon"}.checkList .task:hover{background-color:#eaecf0}.checkList .task:hover .tasksClose{display:block}.checkList .checkbox{margin-top:3px;width:16px;height:16px}.checkList .checkbox label{position:relative;display:flex;justify-content:center;align-items:center;height:100%;outline:3px solid #dfe1e6;border-radius:.5px;cursor:pointer;transition:.3s}.checkList .checkbox label::after{content:"";display:none;font-family:"icomoon";color:#fff;font-size:12px}.checkList .checkbox input:checked~label{background-color:#0079bf;outline:3px solid #0079bf}.checkList .checkbox input:checked~label::after{display:block}.checkList .checkbox input:checked:hover~label{filter:brightness(1.2)}.checkList .checkbox input:not(:checked):hover~label{background-color:#ebecf0}.checkList .progress{position:relative}.checkList .progress .progressBar{margin:14px 0;position:relative;border-radius:10px;height:8px;background-color:#e2e4e9}.checkList .progress .progressBar .fill{border-radius:5px 0 0 5px;position:absolute;top:0;left:0;height:inherit;background-color:#5ba4cf;width:30%;transition:.2s linear}.checkList .progress .percent{position:absolute;left:-30px;font-size:11px;font-weight:300;top:50%;transform:translateY(-50%)}