.checkbox{margin-top:3px;width:16px;height:16px}.checkbox label{position:relative;display:flex;justify-content:center;align-items:center;height:100%;outline:3px solid #dfe1e6;border-radius:.5px;cursor:pointer;transition:.3s}.checkbox label::after{content:"";display:none;font-family:"icomoon";color:#fff;font-size:12px}.checkbox input:checked~label{background-color:#0079bf;outline:3px solid #0079bf}.checkbox input:checked~label::after{display:block}.checkbox input:checked:hover~label{filter:brightness(1.2)}.checkbox input:not(:checked):hover~label{background-color:#ebecf0}