.card{padding:6px 10px;border-radius:5px;background-color:#fff;box-shadow:rgba(9,30,66,.15) 0 6px 12px 0;cursor:pointer;transition:background-color .3s}.card:hover{background-color:#e4e6ea}.card .stat{display:flex;align-items:stretch;gap:20px}.card .stat .comments,.card .stat .checklists,.card .stat .members{position:relative;font-weight:500;font-size:15px;padding-left:18px;line-height:normal;height:14px;line-height:14px}.card .stat .comments::before,.card .stat .checklists::before,.card .stat .members::before{font-family:"icomoon";font-size:14;position:absolute;left:0;bottom:0}.card .stat .comments::before{content:""}.card .stat .checklists::before{content:""}.card .stat .members::before{content:""}